<template>
  <div class="login_container">
    <div class="login_box">
      <!--      -->
      <div class="avatar_box">
        <img src="~assets/logo.png" alt="" />
      </div>
      <!--      -->
      <el-form :rules="loginFormRules" ref="loginFormRef" label-width="0px" class="login_form" :model="loginForm">
        <!--        -->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <!--        -->
        <el-form-item prop="password">
          <el-input type="password" v-model="loginForm.password" prefix-icon="iconfont icon-3702mima"></el-input>
        </el-form-item>
        <!--        -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login" :loading="loginLoading"></el-button>
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      loginLoading: false, // 
      loginForm: {
        // 
        username: 'admin',
        password: '123456'
      },
      loginFormRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        // 
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    resetLoginForm() {
      // ,
      // this.$refs[loginFormRef].resetFields()
      this.$refs.loginFormRef.resetFields()
    },
    login() {
      this.loginLoading = true
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) {
          return (this.loginLoading = false)
        }
        const { data: res } = await this.$http.post('login', this.loginForm)
        if (res.meta.status !== 200) {
          this.loginLoading = false
          return this.$message.error(' !')
        }
        this.$message.success('!')
        // 1.  token, sessionStorage(/) localStorage(/)
        //   1.1 API,
        //   1.2 token ,  token  sessionStorage
        window.sessionStorage.setItem('token', res.data.token)
        // 2. , /home
        this.$router.push('/home')
        this.loginLoading = false
      })
    }
  }
}
</script>

